import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import pic11 from '../assets/images/pic11.jpg'

const Generic = (props) => (
    <div>
        <Helmet>
            <title>Connecting Sponsors and Children to Help End Poverty | Mike Weisert | mikew.design</title>
            <meta name="description" content="Connecting Sponsors and Those in Need to Help End Child Poverty" />
        </Helmet>

        <div id="main" className="alt">
            <section id="banner" className="major ci">
                {/*  
                    <video playsinline autoplay muted loop id="homeBg" width="x" height="y">
                        <source src="static/Chicago.mp4" type="video/mp4" />
                    </video>
                */}
                    <div className="inner">
                    <div className="content">
                        <header className="major">
                            <h1>Connecting Sponsors and Children to Help End Poverty</h1>
                            <div className="row">
                                <div className="12u 12u$(small)"><a href="https://children.org" target="_blank">Children International</a> is a global organization whose mission is to break the cycle of poverty one child at a time. As a Rightpoint consultant, I worked with Children International to redefine their product offering with an eye toward digital engagement and lasting donor relationships.<br/>
                               
                                
                            </div>  </div> <div className="empDates">2015</div>
                        </header>
                    </div>
                </div>
            </section>
            <section id="glance">
            <div className="inner">
                    <div className="content">
                    <h2>Overview</h2> 
                        <div className="row">
                        
                            <div className="self 8u 12u$(small)">
                                <h3>Project At A Glance</h3>
                                <ul>
                                    <li><a href="https://www.webbyawards.com/winners/2018/websites/general/charitable-organizations-non-profit/children-international-redefining-giving-in-a-digital-world/" target="_blank">Webby Peopleâ€™s Voice Award</a> for Best Website in the Charitable Organizations/Non-Profit category in the 22nd Annual Webby Awards</li>
                                    <li>Design of a content platform that increased site visit time to connect sponsors with children in need</li>
                                    <li>Interviewed current CI sponsors and recruited outside audiences to better understand marketplace perception and site needs</li>
                                    <li>Collaborated with the marketing team to create user-experience journeys in the site through the lens of the validated personas established through research</li>
                                    <li>Created guidance and identified opportunities to improve the current online account section and checkout processes</li>
                                </ul>
                            </div>
                        
                            <div className="client 4u 12u$(small)">
                                <h3>About Children International</h3>
                                <ul>
                                    <li>Charitable organization operating for over 80 years</li>
                                    <li>Operating in 10 countries</li>
                                    <li>1M+ kids helped</li>
                                    <li>90% of all funds go directly to programs helping those in need</li>
                                </ul>
                            </div>
                        </div>
                        </div>
                    </div>
            </section>
            <section id="three">
                <div className="inner">
                    <div className="content">
                        <h3>The Approach</h3>
                            <p>
                                As the UX consultant for Rightpoint, my role was to help craft an experience for donors and sponsors that better told the story of how their involvement was improving the lives of children around the globe.  We began by conducting user research with existing sponsors, as well as recruited multiple potential sponsors to better understand their impressions and needs from CI.
                            </p>
                            <p>
                                Going into the project, the marketing team at CI had created a series of proto-personas based on their experiences.  We used these hypothesis in our research and were able to disprove some incorrect assumptions and identify several opportunities, especially as it related to mobile.
                            </p>
                            <p>
                                Through the use of low-fidelity prototypes, combined with high-fidelity comps from our design team, we were able to define a design system comprised of components to be used in their Episerver CMS.  These components allow the marketing team to tell stories on demand to engage audiences and increase donations.
                            </p>
                            <p>
                            At several points during the process, we would travel to the client site (located in Kansas City, MO) to present design, opportunities, and to work through key deliverables.  As part of these sessions, I would often present to their entire team (30+ members) to align the organization on our approach.  By unifying the team on the direction, we were able to get buy-in to the design and quickly adjust to the needs of the organization.
                            </p>

                    </div>
                </div>
            
            </section>

            <section id="four">
                <div className="inner">
                    <Link to="/professional-services" className="button big">Next: Relationship-Based Businesses with One North</Link>
                </div>        
            </section>

        </div>

    </div>
)

export default Generic


